<script>
  export let sectionTitle = "";
  export let sectionSubtitle = "";
</script>

<div class="section__heading">
  <h3 class="section__title">
    <slot name="title">{sectionTitle}</slot>
  </h3>
  <p class="section__subtitle">
    <slot name="subtitle">{sectionSubtitle}</slot>
  </p>
</div>

<style>
  .section__heading {
    position: relative;
    margin: 0 auto;
    padding-bottom: 24px;
    text-align: center;
  }
  .section__heading::after {
    position: absolute;
    content: "";
    bottom: 0;
    right: 0;
    left: 0;
    margin: 0 auto;
    width: 50px;
    height: 2px;
    background-color: var(--primary-color);
  }
  .section__title {
    margin-bottom: 12px;
    font-family: "Phenomena", sans-serif;
    font-weight: 700;
    font-size: 48px;
    line-height: 72px;
    text-transform: uppercase;
  }
  .section__subtitle {
    margin: 0 auto;
    max-width: 660px;
    color: var(--secondary-color);
  }

  @media screen and (max-width: 1239px), (max-device-width: 1239px) {
    
    .section__title {
      font-size: 42px;
      line-height: 56px;
    }
    .section__subtitle {
      max-width: 500px;
    }
  }

  @media screen and (max-width: 510px), (max-device-width: 510px) {
    .section__heading {
      max-width: 320px;
    }
    .section__title {
      margin-bottom: 24px;
      font-size: 42px;
      line-height: 48px;
    }
  }
</style>
