<script>
  function backToTop() {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }
</script>

<div class="top" on:click={backToTop}>
  <img src="images/icons/top.svg" width="24" height="24" alt="Наверх" />
</div>

<style>
  .top {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 10%;
    right: 5%;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: #245081;
    opacity: 0.8;
    box-shadow: 0px 16px 32px rgba(36, 80, 129, 0.32);
    transition: all 0.5s;
    user-select: none;
    cursor: pointer;
    z-index: 99;
    animation: fade 1s ease-out;
  }
  .top:hover {
    background-color: #dd4141;
    box-shadow: 0px 16px 32px rgba(221, 65, 65, 0.32);
    opacity: 1;
  }
  @keyframes fade {
    from {
      transform: translateY(1000%);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 0.8;
    }
  }
</style>
