<script>
  import moment from "moment";
  import "moment/locale/ru";

  //moment.locale('ru');

  export let postDate;
  
  let currentPostDate = new Date(postDate);

  let momentDate = "";
  const monthNames = [
    "января",
    "февраля",
    "марта",
    "апреля",
    "мая",
    "июня",
    "июля",
    "августа",
    "сентября",
    "октября",
    "ноября",
    "декабря",
  ];

  const day = currentPostDate.getDate();
  const month = currentPostDate.getMonth();
  const year = currentPostDate.getFullYear();
  const hours = currentPostDate.getHours();
  const minutes = currentPostDate.getMinutes();
  let currentDate = `${day} ${monthNames[month]} ${year} в ${hours}:${minutes}`;

  const getCurrentDate = (currentPostDate) => {
    return (momentDate = moment(currentPostDate).startOf("hour").fromNow());
  };
  getCurrentDate(currentPostDate);
</script>

<span class="date" title={currentDate}>{momentDate}</span>

<style>
  .date {
    position: relative;
    padding-left: 18px;
    font-size: 12px;
    color: #ffffff;
  }
  .date::before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    bottom: 0;
    margin: auto 0;
    width: 12px;
    height: 12px;
    background-image: url("../images/icons/clock.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
</style>
