<script>
  import SectionHeading from "./SectionHeading.svelte";

  let bgUrl = "content/Services/bg.jpg";
  let sectionTitle = "Дополнительные развлечения и услуги";
</script>

<section
  class="section services"
  id="services"
  style="background-image: url({bgUrl})"
>
  <div class="wrapper">
    <div class="content">
      <SectionHeading>
        <span slot="title">{sectionTitle}</span>
      </SectionHeading>

      <div class="text-block">
        <p>
          Горнолыжный курорт гора Долгая предлагает своим гостям прокат
          горнолыжного оборудования и экипировки, катание на тюбингах, услуги
          инструкторов. Круглогодично функционируют стрелковый стенд,
          пейнтбольное стрельбище, бильярдная комната и боулинг. По будням
          открыт вход в тренажерный зал.
        </p>
        <p>
          В летнее время года на территории комплекса открываются спортивные
          площадки с почасовой арендой и подъемник для велосипедистов, желающих
          проверить себя в маунтибайке.
        </p>
        <p>
          Отдохнуть и перекусить после активно проведенного дня можно в уютном
          кафе «У камина» или в столовой, находящейся в гостинице.
        </p>
        <p>
          Кроме этого гостям предлагаются беседки с мангалом, где можно устроить
          пикник или весело провести время с близкими людьми.
        </p>
      </div>

