<style>
  .services {
    min-height: 1280px;
  }
  .content {
    padding-top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .text-block {
    padding: 92px 0;
    max-width: 700px;
    width: 100%;
  }
  .text-block p:not(:last-child) {
    margin-bottom: 24px;
  }
  .list {
    display: flex;
    flex-flow: row wrap;
    column-gap: 60px;
    padding: 0;
  }
  .item {
    width: 180px;
    min-height: 200px;
  }
  .link {
    display: flex;
    flex-direction: column;
    align-items: center;
    row-gap: 16px;
    padding: 24px 0 32px;
    border-radius: 24px 12px;
    height: 100%;
    background-color: #fff;
    font-weight: 500;
    line-height: 16px;
    text-transform: uppercase;
    text-align: center;
    box-shadow: 0px 8px 32px -16px rgba(36, 80, 129, 0.32);
    overflow: hidden;
    transition: all 0.4s;
  }
  .link:hover {
    background-color: var(--branded-color);
    box-shadow: 0px 8px 32px -16px rgba(221, 65, 65, 1);
  }
  .icon {
    width: 96px;
    height: 96px;
  }
  .icon-services {
    stroke: var(--branded-color);
  }
