<script>
  import { currentToggle } from "../store.js";

  let toggle;

  currentToggle.subscribe((newValue) => (toggle = newValue));
</script>

<button class="menu-button" on:click>
  <span class="menu-button__lines" class:toggle />
</button>

<style>
  .menu-button {
    display: flex;
    align-items: center;
    justify-content: center;
    border: none;
    outline: none;
    width: 24px;
    height: 24px;
    background-color: transparent;
    cursor: pointer;
    transition: all 0.3s;
  }
  .menu-button__lines {
    position: relative;
    display: block;
    height: 1.57px;
    width: 16px;
    background-color: #fff;
    transition: all 0.2s;
  }
  .menu-button__lines::after,
  .menu-button__lines::before {
    position: absolute;
    content: "";
    right: 0;
    height: 1.57px;
    background-color: #fff;
    transition: all 0.2s;
  }
  .menu-button__lines::after {
    top: -5.23px;
    width: 8.52px;
  }
  .menu-button__lines::before {
    bottom: -5.2px;
    width: 11.87px;
  }
  .menu-button:hover .menu-button__lines {
    background-color: #dd4141;
  }
  .menu-button:hover .menu-button__lines::after,
  .menu-button:hover .menu-button__lines::before {
    width: 16px;
    background-color: #dd4141;
  }

  .toggle {
    height: 0;
    transition: none;
  }
  .toggle::after,
  .toggle::before {
    transform: rotate(45deg);
    top: -1px;
    width: 16px;
    background-color: #dd4141;
  }
  .toggle::before {
    transform: rotate(-45deg);
  }
</style>
