<section
  class="section sports-complex"
  id="sportsComplex"
  style="background-image: url({bgUrl})"
>
  <div class="wrapper">
    <SectionHeading>
      <span slot="title">{sectionTitle}</span>
      <span slot="subtitle">{sectionSubtitle}</span>
    </SectionHeading>

    <div class="items">
      {#each items as item}
        <a class="item" href={item.urlMore}>
          <div class="image">
            <svg
              class="item-icon"
              width="128"
              height="128"
              viewBox="0 0 128 128"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              {@html item.svgIconPath}
            </svg>
            <img
              class="item-img"
              src={item.img}
              width="350"
              height="400"
              alt={item.title}
            />
          </div>
          <div class="content">
            <h4 class="title">{item.title}</h4>
            <MediaQuery query="(min-width: 1240px)" let:matches>
              {#if matches}
                <p class="descr">{item.descr}</p>
              {/if}
            </MediaQuery>
          </div>
          <MediaQuery query="(min-width: 1240px)" let:matches>
            {#if matches}
              <a class="more" href={item.urlMore}>Подробнее</a>
            {/if}
          </MediaQuery>
        </a>
      {/each}
    </div>
  </div>
</section>

<style>
  .sports-complex {
    min-height: 1080px;
  }
  .items {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding-top: 100px;
  }
  .item {
    position: relative;
    display: flex;
    flex: 1 100%;
    flex-direction: column;
    align-items: center;
    padding-bottom: 32px;
    border-radius: 24px 12px;
    max-width: 350px;
    min-height: 660px;
    background-color: #fff;
    text-align: center;
    box-shadow: 0px 16px 32px -16px rgba(36, 80, 129, 0.32);
    overflow: hidden;
  }
  .image {
    position: relative;
  }
  .image::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    display: block;
    width: 100%;
    height: 0;
    background: linear-gradient(
      180deg,
      rgba(255, 255, 255, 0.8) 0%,
      #ffffff 100%
    );
    backdrop-filter: blur(2px);
    opacity: 0;
    transition: 0.2s ease-out;
    z-index: 1;
  }
  .item-icon {
    position: absolute;
    top: 96px;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 5;
  }
  .item-img {
    object-fit: cover;
  }
  .content {
    position: relative;
    padding: 0 30px;
    z-index: 2;
    transition: all 0.4s;
  }
  .title {
    padding: 24px 0;
    font-weight: 500;
    font-size: 18px;
    line-height: 21px;
    text-transform: uppercase;
    color: var(--main-color);
    transition: all 0.4s;
  }
  .descr {
    color: var(--secondary-color);
  }
  .more {
    position: relative;
    margin-top: auto;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    text-transform: uppercase;
    color: var(--secondary-color);
    opacity: 0;
    z-index: 2;
  }
  .item:hover .image::after {
    height: 100%;
    opacity: 1;
  }
  .item-icon {
    fill: #fff;
  }
  :global(.item:hover .item-icon) {
    fill: var(--main-color);
  }
  .item:hover .content {
    transform: translateY(-24px);
  }
  .item:hover .title {
    transform: translateY(-12px);
  }
  .item:hover .more {
    opacity: 1;
    animation: fade 0.8s;
  }
  .more:hover {
    color: var(--branded-color);
  }

  @keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  @media screen and (max-width: 1239px), (max-device-width: 1239px) {
    .sports-complex {
      min-height: 784px;
    }
    .items {
      padding-top: 80px;
    }
    .item {
      padding: 0;
      max-width: 236px;
      min-height: 380px;
    }
    .item-icon {
      top: 56px;
    }
    .item:hover .content {
      transform: none;
    }
    .item:hover .title {
      color: var(--branded-color);
      transform: none;
    }
  }

  @media screen and (max-width: 967px), (max-device-width: 967px) {
    .sports-complex {
      background-position: center 14%;
      min-height: auto;
    }
    .items {
      flex-direction: column;
      row-gap: 36px;
      align-items: center;
    }
    .item {
      max-width: 230px;
      min-height: auto;
    }
    .item-icon {
      top: 72px;
      width: 96px;
      height: 96px;
    }
    .item-img {
      max-height: 262px;
    }
  }
</style>
