<script>
  import MediaQuery from "./MediaQuery.svelte";
  import AsideMenu from "./AsideMenu.svelte";
  import BurgerMenuButton from "./elements/BurgerMenuButton.svelte";
  import { currentToggle } from "./store.js";
  import { fade } from "svelte/transition";

  let toggle;

  currentToggle.subscribe((newValue) => (toggle = newValue));

  const link = () => {
    if (toggle) {
      toggle = !toggle;
      currentToggle.update((existing) => toggle);
      document.body.classList.toggle("noscroll");
    }
  };

  const toggleMenu = () => {
    toggle = !toggle;
    currentToggle.update((existing) => toggle);
    document.body.classList.toggle("noscroll");
  };

  let src = "images/logo-white.svg";
  export let altLogo = "Logo";

  let menuItems = [
    { title: "Спортивный комплекс", url: "#sportsComplex" },
    { title: "Гостиничный комплекс", url: "#hotelComplex" },
    { title: "Услуги", url: "#services" },
    { title: "Как добраться", url: "#howReach" },
    { title: "Блог", url: "#newsBlog" },
  ];
</script>

<header class="header">
  <div class="wrapper">
    <div class="header-inner">
      <a class="logo" href="/">
        <img class="logo__img" {src} width="96" height="96" alt={altLogo} />
      </a>

      <nav class="menu">
        <MediaQuery query="(min-width: 1240px)" let:matches>
          {#if matches}
            <ul class="menu__list">
              {#each menuItems as menuItem}
                <li class="menu__item">
                  <a class="menu__link" on:click={link} href={menuItem.url}>
                    {menuItem.title}
                  </a>
                </li>
              {/each}
            </ul>
          {/if}
        </MediaQuery>

        <BurgerMenuButton on:click={toggleMenu} />
      </nav>
    </div>
  </div>
</header>

{#if toggle}
  <div out:fade={{ duration: 300 }}>
    <AsideMenu />
  </div>
{/if}

<style>
  .header {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    z-index: 9;
    background: linear-gradient(rgba(36, 80, 129, 1), rgba(36, 80, 129, 0));
  }
  .header-inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 15px;
  }
  .menu {
    display: flex;
    padding-left: 32px;
    column-gap: 32px;
    align-items: center;
  }
  .menu__list {
    display: flex;
    align-items: center;
    column-gap: 40px;
    padding: 0;
    list-style: none;
  }
  .menu__link {
    position: relative;
    font-weight: 500;
    color: #fff;
  }
  .menu__link:hover {
    color: var(--branded-color);
  }

  @media screen and (max-width: 1239px), (max-device-width: 1239px) {
    .logo__img {
      width: 72px;
      height: 72px;
    }
  }
  @media screen and (max-width: 967px), (max-device-width: 967px) {
    .logo__img {
      width: 64px;
      height: 64px;
    }
  }
</style>
