<script>
  let bgUrl = "content/Hotel/bg.jpg";
  let sectionTitle = "Гостиница";
</script>

<section class="section hotel" style="background-image: url({bgUrl})">
  <div class="wrapper">
    <article class="content">
      <img class="image" src="content/Hotel/content-image.jpg" alt="" />
      <div class="text-block">
        <h3 class="section__title">{sectionTitle}</h3>
        <p>
          Номерной фонд гостиницы насчитывает 37 номеров (132 места). Здесь
          имеются 1-, 2-, 3-, 4-местные комнаты и номера-люкс.
        </p>

        <ul class="rooms__list">
          <li class="rooms__item">
            <img class="rooms__icon" src="content/Hotel/room-1.svg" alt="" />
            <span class="rooms__title">1 местный номер</span>
            <span class="rooms__dots" />
            <span class="rooms__price">2.300 ₽</span>
          </li>

          <li class="rooms__item">
            <img class="rooms__icon" src="content/Hotel/room-2.svg" alt="" />
            <span class="rooms__title">2-х местный номер</span>
            <span class="rooms__dots" />
            <span class="rooms__price">700 ₽</span>
          </li>

          <li class="rooms__item">
            <img class="rooms__icon" src="content/Hotel/room-3.svg" alt="" />
            <span class="rooms__title">3-х местный номер</span>
            <span class="rooms__dots" />
            <span class="rooms__price">550 ₽</span>
          </li>

          <li class="rooms__item">
            <img class="rooms__icon" src="content/Hotel/room-4.svg" alt="" />
            <span class="rooms__title">4-х местный номер</span>
            <span class="rooms__dots" />
            <span class="rooms__price">900 ₽</span>
          </li>

          <li class="rooms__item">
            <img class="rooms__icon" src="content/Hotel/room-lux.svg" alt="" />
            <span class="rooms__title">Номер — люкс</span>
            <span class="rooms__dots" />
            <span class="rooms__price">3.700 ₽</span>
          </li>
        </ul>

        <p>
          Номера имеют все необходимое для полноценного досуга, работы или
          отдыха. Красивый вид на зеленую зону, современная техника, светлый тон
          интерьера, идеально сочетающийся с темными цветами оригинальной мебели
          – все это создает необходимый уровень комфорта для каждого гостя.
        </p>
        <a class="more-link" href="/">Подробнее</a>
      </div>
    </article>
  </div>
</section>

<style>
  .hotel {
    min-height: 976px;
    background-position: top 92px center;
    background-size: contain;
  }
  .content {
    position: relative;
    display: flex;
    justify-content: flex-end;
  }
  .image {
    position: absolute;
    top: 92px;
    left: 0;
    border-radius: 24px 12px;
    max-width: 520px;
    box-shadow: 0px 16px 32px -16px rgba(36, 80, 129, 0.32);
    object-fit: cover;
    z-index: 1;
  }
  .text-block {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 56px 90px 72px 240px;
    border-radius: 24px 12px;
    background-color: #fff;
    width: 100%;
    max-width: 800px;
    box-shadow: 0px 16px 32px -16px rgba(36, 80, 129, 0.32);
  }
  .text-block p {
    padding: 12px 0;
  }
  .rooms__list {
    display: flex;
    flex-direction: column;
    row-gap: 14px;
    padding: 24px 12px;
    width: 100%;
  }
  .rooms__item {
    display: flex;
    align-items: center;
    font-weight: 500;
    text-transform: uppercase;
    white-space: nowrap;
  }
  .rooms__icon {
    margin-right: 12px;
  }
  .rooms__dots {
    display: block;
    margin: 0 6px;
    background-image: url("../content/Hotel/dot.png");
    background-repeat: repeat-x;
    width: 100%;
    height: 2px;
  }
  .rooms__price {
    color: var(--secondary-color);
  }
  .more-link {
    margin-top: 56px;
  }

  @media screen and (max-width: 1239px), (max-device-width: 1239px) {
    .hotel {
      min-height: 820px;
    }
    .image {
      top: 140px;
      max-width: 290px;
    }
    .text-block {
      padding: 32px 48px 56px 120px;
      max-width: 560px;
    }
    .rooms__list {
      row-gap: 12px;
      padding: 12px;
      width: 100%;
    }
  }

  @media screen and (max-width: 967px), (max-device-width: 967px) {
    .hotel {
      padding-top: 64px 0;
      background-size: cover;
      background-position: center;
      min-height: auto;
    }
    .content {
      justify-content: flex-start;
      flex-direction: column;
    }
    .image {
      order: 1;
      position: static;
      inset: auto;
      border-radius: 0 0 24px 12px;
      height: 180px;
      min-width: 100%;
    }
    .text-block {
      padding: 32px 24px 48px 24px;
      border-radius: 24px 12px 0 0;
      max-width: none;
    }
    .rooms__list {
      padding: 0;
    }
  }
</style>
